        stretch: false,
        free: false,
        absLocation: null,
        absLocationFrom: null,
        catchMouseEvents: false,
        loop: false,
        autoStart: false,
        setProperties: function ($xml) {
            var media = this;
            $.each($xml.get(0).attributes, function () {
                var name = this.name;
                var val = this.value;
                switch (name) {
                case 'type':
                    media['mediaType'] = val;
                    break;
                case 'file':
                    media['mediaFileName'] = val;
                    break;
                case 'params':
                    media['externalParam'] = val;
                    break;
                case 'pFrom':
                    media['absLocationFrom'] = val;
                    break;
                case 'buffer':
                    media['recBuffer'] = Number(val);
                    break;
                case 'level':
                case 'from':
                case 'to':
                case 'length':
                    media[name] = Number(val);
                    break;
                case 'px':
                case 'py':
                    if (media.absLocation === null)
                        media.absLocation = new AWT.Point(0, 0);
                    if (name === 'px')
                        media.absLocation.x = Number(val);
                    else
                        media.absLocation.y = Number(val);
                    break;
                case 'stretch':
                case 'free':
                case 'catchMouseEvents':
                case 'loop':
                case 'autostart':
                    media[name] = Utils.getBoolean(val);
                    break;
                }
            });
            return this;
        },
        isEquivalent: function (mc) {
            return this.mediaType === mc.mediaType && (this.mediaFileName === mc.mediaFileName || this.mediaFileName !== null && mc.mediaFileName !== null && this.mediaFileName.toLocaleLowerCase() === mc.mediaFileName.toLocaleLowerCase()) && this.from === mc.from && this.to === mc.to && this.recBuffer === mc.recBuffer;
        },
        getDescription: function () {
            var result = '';
            result += this.mediaType;
            if (this.mediaFileName) {
                result += ' ' + this.mediaFileName;
                if (this.from >= 0)
                    result += ' from:' + this.from;
                if (this.to >= 0)
                    result += ' to:' + this.to;
            } else if (this.externalParam) {
                result += ' ' + this.externalParam;
            }
            return result;
        },
        getIcon: function () {
            var icon = null;
            switch (this.mediaType) {
            case 'PLAY_AUDIO':
            case 'PLAY_RECORDED_AUDIO':
                icon = 'audio';
                break;
            case 'RECORD_AUDIO':
                icon = 'mic';
                break;
            case 'PLAY_VIDEO':
                icon = 'movie';
                break;
            case 'PLAY_MIDI':
                icon = 'music';
                break;
            case 'URL':
                icon = 'url';
                break;
            default:
                icon = 'default';
                break;
            }
            return icon ? MediaContent.icoImg[icon] : null;
        }
