module.exports = function ($, ActivitySequenceJump, Utils) {
    var ActivitySequenceElement = function () {
    };
    ActivitySequenceElement.prototype = {
        constructor: ActivitySequenceElement,
        tag: null,
        description: null,
        activityName: '',
        fwdJump: null,
        backJump: null,
        navButtons: 'both',
        delay: 0,
        setProperties: function ($xml) {
            var ase = this;
            $.each($xml.get(0).attributes, function () {
                var name = this.name;
                var val = this.value;
                switch (name) {
                case 'id':
                    ase['tag'] = Utils.nSlash(val);
                    break;
                case 'name':
                    ase['activityName'] = val;
                    break;
                case 'description':
                case 'navButtons':
                    ase[name] = val;
                    break;
                case 'delay':
                    ase[name] = Number(val);
                    break;
                }
            });
            $xml.children('jump').each(function () {
                var jmp = new ActivitySequenceJump().setProperties($(this));
                if (jmp.id === 'forward')
                    ase.fwdJump = jmp;
                else if (jmp.id === 'back')
                    ase.backJump = jmp;
            });
            return this;
        }
    };
    return ActivitySequenceElement;
}(require('jquery'), require('./ActivitySequenceJump'), require('../Utils'));