                this.$canvas = $('<canvas width="' + rect.dim.width + '" height="' + rect.dim.height + '"/>').css({
                    position: 'absolute',
                    top: 0,
                    left: 0
                });
                this.$div.append(this.$canvas);
                this.bc = new BoxConnector(this, this.$canvas.get(0).getContext('2d'));
                this.invalidate().update();
